.singleColumn{
    margin: 0 10px;

}

.singleColumn__title {
    margin-top: 12px;
    margin-bottom: 24px;
    font-weight: 700;
    font-size: 16px;
    line-height: 15px;
    letter-spacing: 2.4px;
    color: #828FA3 ;

}
.singleColumn__circle{
    display: inline-block;
    margin-right: 12px;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    background-color: black;
}
